.main-nav {
    position: relative;
    background: $hero-bg;
    // Это я временно цвет поставила, потом надо будет transparent
  
    @include for-desktop {
      display: flex;
    }
  }
  .main-nav__logo {
      display: inline-block;
      padding-top: 6px;

      @include for-desktop {
        padding-top: 37px;
        margin-left: -70px;
        margin-right: 100px;
      }
  }
  .main-nav__open-button {
    position: absolute;
    top: 23px;
    right: 20px;
    z-index: 2;
    display: block;
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
  
    @include for-desktop {
      display: none;
    }
  }
  .main-nav__svg {
      width: 21px;
      height: 14px;

      .main-nav__open-button.is-open & {
      width: 17px;
      height: 17px;
      }
  }
  .main-nav__close {
    stroke: $btn-light-color;
  
    opacity: 0;
    visibility: hidden;
  
    transition: visibility $animation, opacity $animation, stroke $animation;
  
    .main-nav__open-button.is-open & {
      opacity: 1;
      visibility: visible;
    }
  }
  .main-nav__open {
    stroke: $accent-dark;
  
    transition: visibility $animation, opacity $animation, stroke $animation;
  
    .main-nav__open-button.is-open & {
      opacity: 0;
      visibility: hidden;
    }
  }
  .main-nav__menu {
    display: flex;
    z-index: 1;
    padding: 60px 0;

    @include phone-and-tablet {
    position: absolute;
      top: 0;
      right: 0;
      width: 250px;
      height: 100vh;
  
      flex-direction: column;
  
      background-color: $mobile-menu-bg;
  
      transform: translateY(-100%);
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
  
      transition: opacity $animation, visibility $animation, transform $animation;
  
      &.is-open {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
        pointer-events: initial;
    }
    }

    @include for-desktop {
        padding: 0;
        flex-direction: row-reverse;
        justify-content: space-between;
        flex-grow: 1;
      }
  }
  .site-nav {
      color: $white-text-color;

      @include for-desktop {
          display: flex;
      }
  }
  .main-nav__button {
    width: 210px;
    height: 44px;
    margin: 0 auto 20px;

    font-weight: 700;
    line-height: 1.31;

    @include for-desktop {
        width: 159px;
        margin: 38px 0 0;
    }
  }
  .site-nav__current {
      color: $accent-dark;
  }
  .site-nav__item {
    padding: 12px 21px;
    border-top: 1px solid #E18298;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.28;

    &:last-child {
        border-bottom: 1px solid #E18298;
    }

    @include for-desktop {
        padding: 50px 0 0;

        &:not(:last-child) {
            padding-right: 35px;
        }
        border: none;
        font-size: 16px;
        line-height: 1.31;

        &:last-child {
            border-bottom: none;
        }
    }
  }